
html,body,#app{
    width:100%;
    height:100%;
    padding:0;
    margin:0;
    overflow:hidden;
}
#app{
    position:fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
}
.content{
    overflow:hidden;
    width:100%;
    height:100%;
    background-repeat:no-repeat;
    background-size:100% 100%;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-pack:center;
        -ms-flex-pack:center;
            justify-content:center;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
    z-index:100;
}
.flex-row, .flex-col{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
}
.flex-row{
    -webkit-box-orient:horizontal;
    -webkit-box-direction:normal;
        -ms-flex-direction:row;
            flex-direction:row;
}
.flex-col{
    -webkit-box-orient:vertical;
    -webkit-box-direction:normal;
        -ms-flex-direction:column;
            flex-direction:column;
}
.tbar, .bbar{
    position:fixed;
    left:0;
    width:100%;
    z-index:99999;
}
.tbar{
    top:0;
}
.bbar{
    bottom:0;
}
.tbar{
    -webkit-box-pack:end;
        -ms-flex-pack:end;
            justify-content:flex-end;
    height:20px;
    background-color:rgba(42, 42, 42, 0.8);
    font-size:12px;
    line-height:12px;
    color:#979A9C;
}
.tbar > *{
    margin-right:18px;
}
.tbar img{
    width:12px;
    height:12px;
    border-radius:7px;
}
.pic{
    background-repeat:no-repeat;
    background-position:center;
    background-size:contain;
    width:100%;
    height:100%;
    background-color:rgba(255,255,255,0.5);
}
.bbar{
    height:44px;
    -ms-flex-pack:distribute;
        justify-content:space-around;
    background-color:rgba(42, 42, 42, 0.9);
}
.bbar-btn{
    padding-top:10px;
    padding-bottom:14px;
    padding-left:14px;
    padding-right:14px;
}
.icon{
    width:20px;
    height:20px;
    background-repeat:no-repeat;
    background-size:100% 100%;
}
.icon-list{
    background-image:url('/static/albums/album-rect.png?v1');
}
.icon-list.active{
    background-image:url('/static/albums/album-rect-on.png?v1');
}
.icon-page{
    background-image:url('/static/albums/album-lr.png?v1');
}
.icon-page.active{
    background-image:url('/static/albums/album-lr-on.png?v1');
}
.icon-music{
    background-image:url('/static/albums/album-music.png?v1');
}
.icon-music.active{
    background-image:url('/static/albums/album-music-on.png?v1');
}
.icon-play{
    background-image:url('/static/albums/album-play.png?v1');
}
.icon-play.active{
    background-image:url('/static/albums/album-stop.png?v1');
}
.list{
    top:0;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient:vertical;
    -webkit-box-direction:normal;
        -ms-flex-flow:column;
            flex-flow:column;
    -webkit-box-pack:end;
        -ms-flex-pack:end;
            justify-content:flex-end;
    overflow-y:auto;
    -webkit-overflow-scrolling: touch;
}
.menu{
    position:fixed;
    left:0;
    bottom:44px;
    width:100%;
    background:rgba(42, 42, 42, 0.9);
    z-index:999999;
}
.list-cont{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient:horizontal;
    -webkit-box-direction:normal;
        -ms-flex-direction:row;
            flex-direction:row;
    -ms-flex-pack:distribute;
        justify-content:space-around;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
    -webkit-overflow-scrolling:touch;
}
.list-item{
    padding:20px 23px;
}
.list-item img{
    width:46px;
}
.menu-bar{
    height:56px;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-pack:center;
        -ms-flex-pack:center;
            justify-content:center;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
}
.xpage-txt, .xpage-icon{
    height:30px;
    line-height:30px;
}
.xpage-txt{
    background:#fff;
    padding:0 6px;
}
.xpage-txt > span{
    padding:0 6px;
}
.page-txt{
    text-align:center;
    color:#fff;
}
.xpage-icon{
    background-repeat:no-repeat;
    background-size:100% 100%;
    width:30px;
}
.xpage-icon.first{
    background-image:url("/static/albums/album-first.png?v1");
}
.xpage-icon.pre{
    background-image:url("/static/albums/album-pre.png?v1");
}
.xpage-icon.next{
    background-image:url("/static/albums/album-next.png?v1");
}
.xpage-icon.last{
    background-image:url("/static/albums/album-last.png?v1");
}
.xpage > *{
    margin:0 10px;
    border:1px solid #727277;
}
.menu-bar input{
    display:inline-block;
    width:20px;
    height:20px;
}
.xcheck{
    margin-left:5px;
    margin-right:30px;
    display:inline-block;
    width:20px;
    height:20px;
    background-size:100% 100%;
    background-image:url("/static/albums/album-check-empty.png?v1");
}
.xcheck.active{
    background-image:url("/static/albums/album-check-ok.png?v1");
}
.xtoggle > *{
    color:#3D3C50;
}
.check-group{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient:horizontal;
    -webkit-box-direction:normal;
        -ms-flex-flow:row;
            flex-flow:row;
    -webkit-box-pack:center;
        -ms-flex-pack:center;
            justify-content:center;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
    color:#727277;
}
.none-bg{
    background:none!important;
}
